import * as React from 'react';
import { Text } from 'native-base';
import { Platform } from 'expo-modules-core';
import {Status as StatusMapper} from '../constants/Status';
import { View } from 'react-native';

import AppliedIcon from '../assets/icons/status/applied.svg';
import DisqualifiedIcon from '../assets/icons/status/disqualified.svg';
import DocumentIssuedIcon from '../assets/icons/status/document-issued.svg';
import FlightIcon from '../assets/icons/status/flight.svg';
import PreQualifiedIcon from '../assets/icons/status/pre-qualified.svg';
import VisaConfirmedIcon from '../assets/icons/status/visa-confirmed.svg';
import VisaProcessIcon from '../assets/icons/status/visa-process.svg';
import VisaSetIcon from '../assets/icons/status/visa-set.svg';
import WorkIssuedIcon from '../assets/icons/status/work-issued.svg';
import WorkStartedIcon from '../assets/icons/status/work-started.svg';

const Icon = ({ children }) => {
  if (Platform.OS === 'web') {
    return <img src={children.type} {...children.props} />;
  }
  return children;
}

// REACT REQUIRE BUGFIX
const StatusIcon = ({name}: {name: string}) => {
  switch (name){
    case 'applied':
      return (
          <Icon><AppliedIcon width="20" height="20" /></Icon>
      )
    case 'disqualified':
      return (
          <Icon><DisqualifiedIcon width="20" height="20"/></Icon>
      )
    case 'document-issued':
      return (
          <Icon><DocumentIssuedIcon width="20" height="20"/></Icon>
      )
    case 'flight':
      return (
          <Icon><FlightIcon width="20" height="20"/></Icon>
      )
    case 'pre-qualified':
      return (
          <Icon><PreQualifiedIcon width="20" height="20" /></Icon>
      )
    case 'visa-confirmed':
      return (
          <Icon><VisaConfirmedIcon width="20" height="20" /></Icon>
      )
    case 'visa-process':
      return (
          <Icon><VisaProcessIcon width="20" height="20" /></Icon>
      )
    case 'visa-set':
      return (
          <Icon><VisaSetIcon width="20" height="20"/></Icon>
      )
    case 'work-issued':
      return (
          <Icon><WorkIssuedIcon width="20" height="20" /></Icon>
      )
    case 'work-started':
      return <Icon><WorkStartedIcon width="20" height="20" /></Icon>
    default:
    return <Text/>
  }
}

const Status: React.FC<{children: string, style: StyleSheet}> = ({children, style}) => {
  const status = StatusMapper(children);
  
  return status ? <View style={{flexDirection:'row', ...style}}>
    <StatusIcon name={status.icon}/>
    <Text style={{...styles.message, color: status.color}}>{status.name}</Text>
  </View> : <View/>
}

const styles = {
  message: {
    fontSize: 15
  }
}

export default Status;